import LingLoading from '@/components/attendance/LingLoading';
export default {
  data() {
    return {
      studentInfo: [],
      state: {
        isShow: true,
        message: '加载中',
        state: 'loading'
      },
    }
  },
  created() {
    this._initGrowthRecord();
  },
  methods: {
    _initGrowthRecord() {
      let userInfo = JSON.parse(sessionStorage.getItem('CURRENT_USER'));
      this.$http.post(`http://120.25.123.8:8080/video/rest/post/student_grow_record/${userInfo.studentId}`).then((response) => {
        response = response.data;
        if( response.success) {
          this.studentInfo = response.data.list[0];
        }
      });
    },
    fetchData() {
      this._initGrowthRecord();
    }
  },
  components: {
    LingLoading
  }
}



